@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'IBM Plex Mono', monospace;
}

button {
  font-family: 'IBM Plex Mono', monospace;
  color:#265d77;
  background-color: #b7e4f8;
  font-weight: bold;
  border-radius: .5em;
  border: none;
}


input {
  font-family: 'IBM Plex Mono', monospace;
  font-style: italic;
  border: none;
  border: 1px solid grey;
  border-radius: .2em;
}

::-webkit-input-placeholder {
  color: black;
  opacity: 1;
}

:-ms-input-placeholder {
  color: black;
  opacity: 1;
}

::placeholder {
  color: black;
  opacity: 1;
}

.header-text {
  font-size: 1.5em;
  font-weight: bold;
}

.header-button {
  padding: .5em 1em .6em 1em;
  font-size: 1em;
  margin: 1em 0 0 1em;
}

.header-button:hover {
  color: #b7e4f8;
  background-color: #265d77;
}

.app {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 860px;
  border-left: 1px solid black;
  border-right: 1px solid black;
  height: 100vh;
  overflow: auto;
}

.empty {
  border: 1px solid red
}

.btn-container {
  align-self: center;
  display: flex;
  flex-direction: row;
  width: 160px;
  justify-content: space-around;
}

.main-button {
  font-size: 1.3em;
  padding: .3em .5em;
}
.contact-info label {
  display: none;
}

.contact-info form {
  display: flex;
  flex-direction: row;
}

.contact-info {
  height: 175px;
  margin: 4em 4em 0 4em;
  padding-bottom: 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid black;
  box-sizing: border-box;
}

.name-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.name-input {
  height: 30px;
  width: 300px;
  font-size: 2em;
  margin: 0 .5em 0 0;
}

.name {
  font-size: 2em;
}

.name-container button {
  align-self: center;
  height: 22px;
}

.occupation-input {
  height: 22px;
  font-size: 1.5em;
  margin: 0 .5em 0 0;
}


.occupation {
  font-size: 1.5em;
  font-weight: bold;
  font-style: italic;
}

.address-container {
  display: flex;
  flex-direction: column;
  text-align: end;
  justify-content: space-around;
  padding: 0 0 1em 0;
}

.address-input {
  height: 14px;
  font-size: 1em;
  text-align: end;
  width: 300px;
  margin-left: .5em;
}


.skill-section {
  margin: 2em 4em;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid black;
  padding-bottom: 2em;
  box-sizing: border-box;
}

.no-preview {
  min-height:150px;
}

.skill-section .header {
  flex-grow: 1;
  flex-basis: 225px;
}

.skill-section label {
  display: none;
}

.skill-section input {
  width: 200px;
  font-size: 1em;
  margin-right: 1em;
  padding-left: .3em;
}

.skill-form {
  display: flex;
  flex-direction: row;
  margin-top: 2em;
}

.skill-form button {
  align-self: center;
  font-size: 1.2em;
  padding: .1em .5em .2em .5em;
}

.skill-container-no-preview {
  width: 500px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  flex-grow: 2;
}

.skill-container {
  margin-top: 1em;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
}

.skill {
  background-color: #b7e4f8;
  color: #265d77;
  font-weight: bold;
  border-radius: 1em;
  margin: .5em .5em .5em 0;
  padding: .7em;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 25px;
}

.skill-no-preview:hover {
  background-color: #b1bbc0;
}


.work-section {
  margin: 0 4em 2em 4em;
}

.work-container {
  margin-top: .5em;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid black;
  padding-bottom: 2em;
}

.work {
  display: flex;
  flex-direction: column;
  padding: 1em;
}

.work-form {
  margin: 2em 3em; 
  padding: 1em;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 1em;
}

.work-form label {
  font-weight: bold;
  margin: 1em;
}

.cancel-button {
  width: 30px;
  font-size: 1em;
}

.work-input {
  width: 95%;
  align-self: center;
  font-size: 1em;
  padding-left: .5em;
}

.date-container {
  align-self: center;
  margin: 3em 0 2em 0;
  display: flex;
  flex-direction: row;
}

.date-input {
  font-size: 1em;
}

.create-work-btn {
  margin-top: 1em;
  height: 25px;
  width: 150px;
  font-size: 1em;
  align-self: center;
}

.organization, .organization-input {
  font-weight: bold;
  font-size: 1.3em;
  margin: .9em 0;
}

.position, .position-input {
  font-style: italic;
  font-size: 1.1em;
}

.timing {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: .9em;
  margin: .8em 0;
}

.work-input.description  {
  border: 1px solid grey;
  border-radius: .5em;
  resize: none;
  font-family: 'IBM Plex Mono', monospace;
}

.edit-input {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.edit-input label {
  display: none;
}

.edit-input button {
  margin: 0 .5em;
  font-size: .8em;
  height: 28px;
}

.organization-input {
  height: 18px;
}

.position-input {
  height: 18px;
}

.from-input, .to-input {
  font-size: .9em;
}

.description-input {
  resize: none;
  font-size: 1em;
  font-family: 'IBM Plex Mono', monospace;
  width: 650px;
  height: 300px
}
.reference-section {
  margin: 0 4em 2em 4em;
  display: flex;
  flex-direction: row;
}

.reference-section .header {
  flex-basis: 240px;
}

.reference-form {
  margin: 1em 0;
  padding: 1em;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 1em;
}

.reference-form label {
  font-weight: bold;
  margin: 1em 0 .5em 0;
}

.reference-form button {
  margin-left: 0;
  align-self: center;
  margin-top: 1em;
  font-size: 1em;
  width: 150px;
}

.reference-form input {
  font-size: 1em;
}

.reference-container {
  padding: 1em 3em;
}

.reference-name {
  font-weight: bold;
  font-size: 1.3em;
  margin-bottom: .3em;
}
.reference-email {
  font-style: italic;
  margin: .5em 0;
}

.reference {
  margin: 1em 0;
}

.reference-edit-form label {
  display: none;
}

.reference-edit-form {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.reference-edit-form button  {
  height: 22px;
}

.reference-edit-form input {
  margin-right: .5em;
}

.edit-name {
  font-size: 1.2em;
  height: 18px;
}

.edit-relationship, .edit-email, .edit-phone {
  font-size: 1em;
  height: 14px;
}

.edit-email  {
  margin: .5em 0;
  width: 250px;
}

